@import '../../../scss/mixins';

.hb-article-time {
  display: flex;
  align-items: center;
  background-color: var(--petrol-5);
  border-radius: 8px;
  padding: 6px 10px 6px 8px;
  color: var(--petrol-80);
  font-size: 20px;
}

.hb-article-time-text {
  @include hbFormType;
  color: var(--nightblue-100);
  margin-left: 8px;
}

.hb-categories-wrapper {
  margin-top: 28px;
  border-radius: 30px;
  padding: 30px;
  border: 2px solid var(--petrol-80);
  @media screen and (max-width: 860px) {
    &:last-child {
      margin-top: 24px;
    }
  }
  @media screen and (max-width: 480px) {
    padding: 20px;
  }

  .label {
    margin-bottom: 22px;
    font-size: 19px;
    font-style: normal;
    font-weight: 500;
    line-height: 25px;
    letter-spacing: -0.01em;
    text-align: left;
    color: white;
    @media screen and (max-width: 860px) {
      margin-bottom: 14px;
    }
  }

  .items-wrapper {
    display: flex;
    flex-wrap: wrap;

    .item {
      max-width: 100%;
      padding: 6px 12px;
      background-color: var(--petrol-80);
      font-size: 11px;
      font-style: normal;
      font-weight: 500;
      line-height: 18px;
      letter-spacing: 0.01em;
      color: var(--aquamarin-80);
      border-radius: 80px;
      margin: 10px 8px 0 0;
      display: inline-block;
      box-shadow: 0px 5px 10px rgba(0, 95, 113, 0.4);
      white-space: nowrap;
      cursor: pointer;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .item:hover {
      background-color: var(--petrol-60);
    }

    .item.active {
      background-color: var(--violet-80);
      color: white;
      box-shadow: 0px 5px 10px rgba(99, 70, 117, 0.4);
    }

    .item.active:hover {
      background-color: var(--violet-60);
    }
  }
}

.hb-my-categories-wrapper {
  margin-top: 28px;
  padding: 30px;
  background: linear-gradient(129.17deg, #e9daf2, #faf2ff);
  border-radius: 30px;
  padding: 30px 26px 18px;
  @media screen and (max-width: 860px) {
    margin-top: 24px;
  }
  @media screen and (max-width: 480px) {
    padding: 20px;
  }

  .label {
    margin-bottom: 22px;
    font-size: 19px;
    font-style: normal;
    font-weight: 500;
    line-height: 25px;
    letter-spacing: -0.01em;
    text-align: left;
    color: var(--nightblue-100);
    padding: 0 8px;
    @media screen and (max-width: 860px) {
      margin-bottom: 14px;
    }
  }

  .items-wrapper {
    display: flex;
    flex-wrap: wrap;
    padding: 0 8px 12px 8px;

    .item {
      max-width: 100%;
      padding: 6px 12px;
      background-color: var(--violet-80);
      font-size: 11px;
      font-style: normal;
      font-weight: 500;
      line-height: 20px;
      letter-spacing: 0.01em;
      color: white;
      border-radius: 80px;
      margin: 10px 8px 0 0;
      box-shadow: 0px 5px 10px rgba(99, 70, 117, 0.4);
      white-space: nowrap;
      cursor: pointer;
      & > div > div {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }

    .item:hover {
      background-color: var(--violet-60);
    }
  }
}

.hb-start-newsfeed-wrapper {
  width: 100%;
  padding: 22px 10px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 0;
  z-index: 3;
}

.hb-start-newsfeed {
  width: 100%;
  max-width: 325px;

  button {
    @include hbButtonText;
  }
}

.hb-select-category-intro {
  margin: 28px 0 34px;
  text-align: center;
  & > p {
    @include hbIntroText;
    margin: 0;
    max-width: 565px;
    display: inline-block;
    text-align: center;
    color: white;
  }
  @media screen and (max-width: 860px) {
    margin: 24px 0 30px;
  }
  @media screen and (max-width: 480px) {
    margin: 19px 0 20px;
  }
}

.Newsfeed {
  &-title {
    padding: 0 20px;
    color: white;
    text-align: center;
  }
  &-wrapperBtn {
    & > button {
      @media screen and (max-width: 480px) {
        padding: 0 11px;
      }
    }
  }
  &-content {
    @media screen and (max-width: 480px) {
      margin: 0 0 30px;
    }
    .hb-card-item {
      margin: 13px 0 0;
      &:first-child,
      &:nth-child(2),
      &:nth-child(3) {
        margin-top: 0;
      }
      @media screen and (max-width: 991px) {
        &:nth-child(3) {
          margin-top: 13px;
        }
      }
      @media screen and (max-width: 860px) {
        margin-top: 26px;
        max-width: initial;
        width: 100%;
        flex: initial;
        &:nth-child(2),
        &:nth-child(3) {
          margin-top: 26px;
        }
      }
      @media screen and (max-width: 480px) {
        margin-top: 20px !important;
        &:first-child {
          margin-top: 0 !important;
        }
      }
    }
    &.favorite {
      .hb-card-item {
        @media screen and (max-width: 860px) {
          max-width: initial;
          width: 50%;
          flex: initial;
          &:nth-child(2) {
            margin-top: 0;
          }
        }
        @media screen and (max-width: 640px) {
          margin-top: 20px !important;
          width: 100%;
          &:first-child {
            margin-top: 0 !important;
          }
        }
      }
      & .subFavorite {
        .hb-card-item {
          margin-top: 30px;
          &:first-child,
          &:nth-child(2),
          &:nth-child(3) {
            margin-top: 0;
          }
          @media screen and (max-width: 991px) {
            &:nth-child(3) {
              margin-top: 30px;
            }
          }
          @media screen and (max-width: 640px) {
            margin-top: 14px !important;
            &:first-child {
              margin-top: 0 !important;
            }
          }
        }
      }
    }
    &.basic {
      & .Article {
        &-thumbnail {
          @media screen and (max-width: 860px) {
            height: 550px !important;
          }
          @media screen and (max-width: 480px) {
            height: 251px !important;
          }
        }
      }
    }
    & > .row {
      padding: 30px;
      @media screen and (max-width: 480px) {
        padding: 20px;
      }
    }
  }
  &-actions {
    padding: 0 20px;
    margin-bottom: 30px !important;
  }
}

.NewsFeedIntro {
  &-wrapper {
    margin: 20px 0;
    padding: 48px 24px;
    color: #fff;
    @media screen and (max-width: 480px) {
      padding: 0 20px;
      width: 100%;
      border: none !important;
    }
  }
  &-desc {
    @include hbIntroText;
    margin: 16px 0;
  }
}

.NewsfeedSelectCategory {
  &-content {
    padding-bottom: 30px;
    @media screen and (max-width: 860px) {
      & > div {
        max-width: initial;
        width: 100%;
        flex: initial;
      }
    }
  }
}
